body {
  font: 100% Helvetica, sans-serif;
  color: #333;
}

.btn-blue {
  color: white;
  border: none;
  font-weight: 400;
  padding: 5px 10px;
  background-color: #25f;
  transition: background-color 200ms ease-in-out;
}

.btn-blue:hover {
  opacity: 0.8;
}

.btn-red {
  color: white;
  border: none;
  font-weight: 400;
  padding: 5px 10px;
  background-color: #f25;
  transition: background-color 200ms ease-in-out;
}

.btn-red:hover {
  opacity: 0.8;
}

.btn-white {
  color: white;
  border: none;
  font-weight: 400;
  padding: 5px 10px;
  background-color: #dfdfdf;
  transition: background-color 200ms ease-in-out;
}

.btn-white:hover {
  opacity: 0.8;
}

.btn-black {
  color: white;
  border: none;
  font-weight: 400;
