  background-color: #0f0f0f;
  transition: background-color 200ms ease-in-out;
}

.btn-black:hover {
  opacity: 0.8;
}

.btn-yellow {
  color: white;
  border: none;
  font-weight: 400;
  padding: 5px 10px;
  background-color: #fd5;
  transition: background-color 200ms ease-in-out;
}

.btn-yellow:hover {
  opacity: 0.8;
}

.rounded-md {
  border-radius: 10px;
}

.rounded-sm {
  border-radius: 5px;
}

.rounded-xl {
  border-radius: 15px;
}

.rounded-2xl {
  border-radius: 20px;
}

.border-md {
  border: 5px solid black;
}

.border-sm {
  border: 2px solid black;
}

.border-xl {
  border: 10px solid black;
}

.border-2xl {
  border: 15px solid black;
}

.color-blue {
  color: #25f;
}

.border-blue {
  border-color: #25f;
}

.color-red {
  color: #f25;
}

.border-red {
  border-color: #f25;
}

