}

.border-black {
  border-color: #0f0f0f;
}

.color-yellow {
  color: #fd5;
}

.border-yellow {
  border-color: #fd5;
}

.center {
  text-align: center;
}

.flex {
  display: flex;
}

.flex-center {
  justify-content: center;
  align-items: center;
}

.absolute-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%, -50%);
}

